<template>
  <v-container class="section" id="portfolio">
    <h1 class="display-2 font-weight-bold text-center">PORTFOLIO</h1>
    <p class="text--secondary text-center font-italic">
      Here you can see some of my projects
    </p>

    <v-row class="mt-2">
      <v-spacer></v-spacer>
      <v-col cols="4">
        <v-select
          v-model="category"
          :items="categories"
          label="Category"
          outlined
        ></v-select>
      </v-col>
      <v-spacer></v-spacer>
    </v-row>

    <v-row>
      <v-col
        class="d-flex justify-center poster-project"
        v-for="project in filteredProjects"
        :key="project.id"
      >
        <v-hover v-slot="{ hover }">
          <v-card width="300">
            <v-img :src="project.img" height="300px">
              <v-avatar
                color="orange"
                class="ma-2"
                v-if="project.relevance == 10"
              >
                <v-icon dark> mdi-star </v-icon>
              </v-avatar>
              <v-expand-transition>
                <div
                  v-if="hover"
                  class="
                    d-flex
                    transition-fast-in-fast-out
                    amber
                    darken-2
                    v-card--reveal
                    text-h2
                    white--text
                  "
                  style="height: 100%"
                >
                  <label class="more-info" @click="openViewer(project.id)">Click Me!</label>
                </div>
              </v-expand-transition>
            </v-img>

            <v-card-title class="text-center d-block">{{
              project.title
            }}</v-card-title>
            <v-card-subtitle class="text-center d-block">
              {{ project.technology }}
              <br />
              {{ project.subtitle }}
            </v-card-subtitle>
          </v-card>
        </v-hover>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { projects } from "../data.js";
import Viewer from "viewerjs";

export default {
  name: "Portfolio",
  data: () => ({
    projects,
    category: "The best",
    categories: ["The best", "Flutter and Dart", "Vue 2.x"],
  }),
  computed: {
    filteredProjects() {
      if (this.category == "The best")
        return this.projects.filter((proj) => proj.relevance == 10);

      return this.projects.filter((proj) => this.category == proj.technology);
    },
  },
  methods: {
    openViewer(projectId) {
      var gallery = this.projects.find((proj) => proj.id == projectId).gallery;

      var div = document.createElement("div");

      gallery.forEach((photo) => {
        var image = new Image();
        image.src = photo;
        div.appendChild(image);
      });

      new Viewer(div).show();
    },
  },
};
</script>

<style scoped>
.more-info {
  cursor: pointer;
}
.more-info:hover{
    text-decoration: underline;
}

.v-card--reveal {
  align-items: center;
  bottom: 0;
  justify-content: center;
  position: absolute;
  width: 100%;
}
</style>
